<template>
    <div id="dataAnalysis">
        <h3 class="picTitle">大厅今日数据</h3>
        <div style="width: 94%;margin: 0 auto;">
            <el-row :gutter="40">
                <el-col :span="12">
                    <div class="itemWarp">
                        <h4 class="todayTitle">大厅取号总量</h4>
                        <p class="todayContent">{{ realData.Total }}</p>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="itemWarp">
                        <h4 class="todayTitle">今日接件量</h4>
                        <p class="todayContent">{{ realData.didBNum }}</p>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="itemWarp">
                        <h4 class="todayTitle">今日取号量</h4>
                        <p class="todayContent">{{ realData.pickNum }}</p>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="itemWarp">
                        <h4 class="todayTitle">今日转号量</h4>
                        <p class="todayContent">{{ realData.moveNum }}</p>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="itemWarp">
                        <h4 class="todayTitle">等待人数</h4>
                        <p class="todayContent">{{ realData.waitNum }}</p>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="itemWarp">
                        <h4 class="todayTitle">平均等待时间</h4>
                        <p class="todayContent">{{ realData.waitTime }}</p>
                    </div>
                </el-col>
            </el-row>
        </div>
        <h3 class="picTitle">今日事项Top 10</h3>
        <div style="width: 94%;margin: 0 auto;">
            <el-table
                    :data="tableData"
                    style="width: 100%">
                <el-table-column
                        prop="index"
                        label="序号"
                        width="60">
                </el-table-column>
                <el-table-column
                        label="事项"
                        min-width="240">
                    <template slot-scope="scope">
                        <el-button
                                type="text"
                                @click="cellClickFn(scope.$index, scope.row)">{{ scope.row.matter }}</el-button>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="peoperNum"
                        label="件数">
                </el-table-column>
                <el-table-column
                        prop="per"
                        label="占比">
                </el-table-column>
            </el-table>
        </div>
        <div name="时间段的取号情况">
            <h3 class="picTitle">时间段的取号情况</h3>
            <el-form>
                <el-row>
                    <el-col :span="6">
                        <el-form-item label="日期">
                            <el-date-picker
                                    v-model="date1"
                                    type="date"
                                    format="yyyy-MM-dd"
                                    value-format="yyyy-MM-dd"
                                    placeholder="选择日期" @change="searchNumberChangeFn">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div style="text-align: center;">
                <el-row :gutter="20">
                    <el-col :span="24">
                        <div class="picWarp" id="pic1"></div>
                    </el-col>
                </el-row>
            </div>
        </div>
        <div name="大厅各主题办件数据分析">
            <h3 class="picTitle">大厅各主题办件数据分析</h3>
            <el-form>
                <el-row>
                    <el-col :span="6">
                        <el-form-item label="日期">
                            <el-date-picker
                                    v-model="date2"
                                    type="date"
                                    format="yyyy-MM-dd"
                                    value-format="yyyy-MM-dd"
                                    placeholder="选择日期"
                                    @change="searchPassDealChangeFn">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div style="text-align: center;">
                <el-row :gutter="20">
                    <el-col :span="24">
                        <div class="picWarp" id="pic2"></div>
                    </el-col>
                </el-row>
            </div>
        </div>
        <div name="工作人员办件数据分析">
            <h3 class="picTitle">工作人员办件数据分析</h3>
            <el-form :model="form1">
                <el-row :gutter="20">
                    <el-col :span="6">
                        <el-form-item label="日期">
                            <el-date-picker
                                    v-model="form1.date3"
                                    type="date"
                                    format="yyyy-MM-dd"
                                    value-format="yyyy-MM-dd"
                                    placeholder="选择日期"
                                    style="width:80%;"
                                    @change="searchPersonTimeChangeFn">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="部门">
                            <el-select v-model="form1.theme" @change="changeThemeFn" placeholder="请选择部门" style="width:80%;">
                                <el-option v-for="(item, index) in themeList" :key="index" :label="item.label" :value="item.label"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="事项">
                            <el-select v-model="form1.matter" @change="changeMatterFn" placeholder="请选择事项" style="width:80%;">
                                <el-option v-for="(item, index) in matterList" :key="index" :label="item.label" :value="item.label"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="员工">
                            <el-select v-model="form1.person" @change="searchPersonChangeFn" placeholder="请选择员工" style="width:80%;">
                                <el-option v-for="(item, index) in personList" :key="index" :label="item.label" :value="item.id"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div style="text-align: center;">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <div class="picWarp" id="pic3"></div>
                    </el-col>
                    <el-col :span="12">
                        <div class="picWarp" id="pic4"></div>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
import supportFun from '@/supportFunction/index'
import store from '@/store/index'

var dataAnalysisTimer = ''
export default {
  name: 'dataAnalysis',
  data () {
    return {
      realData: {
        Total: '',
        didBNum: '',
        moveNum: '',
        pickNum: '',
        waitNum: '',
        waitTime: ''
      },
      tableData: [],
      dataAnalysisData: {},
      date1: supportFun.formatTimeOne(new Date().getTime()),
      date2: supportFun.formatTimeOne(new Date().getTime()),
      form1: {
        date3: supportFun.formatTimeOne(new Date().getTime()),
        theme: '',
        matter: '',
        person: ''
      },
      themeList: [],
      matterList: [],
      personList: []
    }
  },
  created () {
    this.getThemeFn()
    this.getDataAnalysisFn()
  },
  mounted () {
    this.searchNumberChangeFn()
    this.setIntervalFn()
  },
  methods: {
    setIntervalFn () {
      let self = this
      this.searchPassDealChangeFn()
      clearInterval(dataAnalysisTimer)
      dataAnalysisTimer = setInterval(function () {
        self.searchPassDealChangeFn()
      }, 1000 * 60 * 3)
    },
    searchNumberChangeFn () {
      let temp = {
        date: this.date1,
        user_id: sessionStorage.user_id
      }
      console.log(temp)
      this.TakeNumFn(temp)
    },
    async TakeNumFn (params) {
      try {
        let res = await this.$api.unsplash.postTakeNum(params)
        console.log(res)
        if (res.errno === '0') {
          this.barAndLineThree('pic1', res.data)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    searchPassDealChangeFn () {
      let temp = {
        date: this.date2,
        user_id: sessionStorage.user_id
      }
      console.log(temp)
      this.PassDealFn(temp)
    },
    async PassDealFn (params) {
      try {
        let res = await this.$api.unsplash.postPassDeal(params)
        console.log(res)
        if (res.errno === '0') {
          this.barAndLineFour('pic2', res.data)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    searchPersonChangeFn () {
      let temp = {
        date: this.form1.date3,
        subject: this.form1.theme,
        item: this.form1.matter,
        people_id: this.form1.person,
        user_id: sessionStorage.user_id
      }
      console.log(temp)
      this.PersonDealFn(temp)
    },
    searchPersonTimeChangeFn () {
      if (this.form1.person) {
        let temp = {
          date: this.form1.date3,
          subject: this.form1.theme,
          item: this.form1.matter,
          people_id: this.form1.person,
          user_id: sessionStorage.user_id
        }
        this.PersonDealFn(temp)
      }
    },
    async PersonDealFn (params) {
      try {
        let res = await this.$api.unsplash.postPersonDeal(params)
        console.log(res)
        if (res.errno === '0') {
          let temp = res.data
          this.barAndLineOne('pic3', temp.week)
          this.barAndLineTwo('pic4', temp.timeSlot)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    changeThemeFn () {
      // for (let i = 0; i < this.themeList.length; i++) {
      //   if (this.themeList[i].label === this.form1.theme) {
      //     this.getMatterFn(this.themeList[i].label)
      //     break
      //   }
      // }
      this.getMatterFn(this.form1.theme)
    },
    changeMatterFn () {
      // for (let i = 0; i < this.matterList.length; i++) {
      //   if (this.matterList[i].label === this.form1.matter) {
      //     // this.selectMatterName = this.matterList[i].label
      //     break
      //   }
      // }
      let params = {
        subject: this.form1.theme,
        item: this.form1.matter,
        user_id: sessionStorage.user_id
      }
      this.getPersonFn(params)
    },
    async getDataAnalysisFn () {
      try {
        let params = {}
        let res = await this.$api.unsplash.postDataAnalysis(params)
        if (res.errno === '0') {
          this.dataAnalysisData = res.data
          this.realData = res.data.realData
          let temp = res.data.matterTop5.data.slice(0, 10)
          this.tableData = supportFun.addSequence(temp)
          console.log(11, this.tableData)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async getThemeFn () {
      try {
        let params = {}
        let res = await this.$api.unsplash.getSubjects(params)
        console.log(res)
        this.themeList = []
        if (res.errno === '0') {
          for (let i = 0; i < res.data.subjects.length; i++) {
            this.themeList.push({
              label: res.data.subjects[i].name,
              val: res.data.subjects[i].id
            })
          }
          if (this.themeList.length) {
            this.form1.theme = this.themeList[0].label
            this.getMatterFn(this.themeList[0].label)
          } else {
            this.form1.theme = ''
          }
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async getMatterFn (theme) {
      try {
        let params = {
          subject: theme
        }
        let res = await this.$api.unsplash.getMatters(params)
        console.log(res)
        if (res.errno === '0') {
          this.matterList = []
          for (let i = 0; i < res.data.items.length; i++) {
            this.matterList.push({
              label: res.data.items[i].name,
              val: res.data.items[i].id
            })
          }
          if (this.matterList.length) {
            this.form1.matter = this.matterList[0].label
            let aa = {
              subject: this.form1.theme,
              item: this.form1.matter,
              user_id: sessionStorage.user_id
            }
            this.getPersonFn(aa)
          } else {
            this.form1.matter = ''
          }
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async getPersonFn (params) {
      try {
        let res = await this.$api.unsplash.postUsers(params)
        console.log(res)
        if (res.errno === '0') {
          this.personList = []
          for (let i = 0; i < res.data.length; i++) {
            this.personList.push({
              label: res.data[i].name,
              id: res.data[i].id
            })
          }
          if (this.personList.length) {
            this.form1.person = ''
          } else {
            this.form1.person = ''
          }
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    barAndLineOne (id, obj) {
      let legend = obj.legend
      let xAxis = obj.xAxis
      let yAxis1 = obj.yAxis1
      // let yAxis2=obj.yAxis2
      // let yAxis3=obj.yAxis3

      let temp = yAxis1.data
      let max = Math.max.apply(null, temp)

      var Chart = this.$echarts.init(document.getElementById(id))
      var option = {
        title: {
          text: legend,
          left: 'center',
          top: '4%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        grid: {
          top: '30%',
          bottom: '20%'
        },
        toolbox: {
          show: false,
          feature: {
            dataView: {show: true, readOnly: false},
            magicType: {show: true, type: ['line', 'bar']},
            restore: {show: true},
            saveAsImage: {show: true}
          }
        },
        legend: {
          show: false,
          data: legend,
          left: 'center',
          top: '10%'
        },
        xAxis: [
          {
            type: 'category',
            data: xAxis,
            axisPointer: {
              type: 'shadow'
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '件数',
            min: 0,
            max: max,
            // interval: 50,
            minInterval: 1,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: yAxis1.name,
            type: 'bar',
            data: yAxis1.data,
            barWidth: '50%',
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: 'top',
                  textStyle: { // 数值样式
                    color: 'red',
                    fontSize: 16
                  }
                }
              }
            }
          }
        ]
      }
      Chart.setOption(option)
    },
    barAndLineTwo (id, obj) {
      let legend = obj.legend
      let xAxis = obj.xAxis
      let yAxis1 = obj.yAxis1
      // let yAxis2=obj.yAxis2
      // let yAxis3=obj.yAxis3
      var Chart = this.$echarts.init(document.getElementById(id))
      var option = {
        title: {
          text: legend,
          left: 'center',
          top: '4%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        grid: {
          top: '30%',
          bottom: '20%'
        },
        toolbox: {
          show: false,
          feature: {
            dataView: {show: true, readOnly: false},
            magicType: {show: true, type: ['line', 'bar']},
            restore: {show: true},
            saveAsImage: {show: true}
          }
        },
        legend: {
          show: false,
          data: legend,
          left: 'center',
          top: '10%'
        },
        xAxis: [
          {
            type: 'category',
            data: xAxis,
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              interval: 0,
              rotate: 45
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '件数',
            min: 0,
            // max: 250,
            // interval: 50,
            minInterval: 1,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: yAxis1.name,
            type: 'bar',
            data: yAxis1.data,
            barWidth: '50%',
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: 'top',
                  textStyle: { // 数值样式
                    color: 'red',
                    fontSize: 16
                  }
                }
              }
            }
          }
        ]
      }
      Chart.setOption(option)
    },
    barAndLineThree (id, obj) {
      let legend = obj.legend
      let xAxis = obj.xAxis
      let yAxis1 = obj.yAxis1
      // let yAxis2=obj.yAxis2
      // let yAxis3=obj.yAxis3
      var Chart = this.$echarts.init(document.getElementById(id))
      var option = {
        title: {
          text: legend,
          left: 'center',
          top: '4%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        grid: {
          top: '30%',
          bottom: '20%'
        },
        toolbox: {
          show: false,
          feature: {
            dataView: {show: true, readOnly: false},
            magicType: {show: true, type: ['line', 'bar']},
            restore: {show: true},
            saveAsImage: {show: true}
          }
        },
        legend: {
          show: false,
          data: legend,
          left: 'center',
          top: '10%'
        },
        xAxis: [
          {
            type: 'category',
            data: xAxis,
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              interval: 0,
              rotate: 45
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '取号量',
            min: 0,
            // max: 250,
            // interval: 50,
            minInterval: 1,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: yAxis1.name,
            type: 'bar',
            data: yAxis1.data,
            barWidth: '50%',
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: 'top',
                  textStyle: { // 数值样式
                    color: 'red',
                    fontSize: 16
                  }
                }
              }
            }
          }
        ]
      }
      Chart.setOption(option)
    },
    barAndLineFour (id, obj) {
      let legend = obj.legend
      let xAxis = obj.xAxis
      let yAxis1 = obj.yAxis1
      // let yAxis2=obj.yAxis2
      // let yAxis3=obj.yAxis3
      var Chart = this.$echarts.init(document.getElementById(id))
      var option = {
        title: {
          text: legend,
          left: 'center',
          top: '4%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        grid: {
          top: '30%',
          bottom: '20%'
        },
        toolbox: {
          show: false,
          feature: {
            dataView: {show: true, readOnly: false},
            magicType: {show: true, type: ['line', 'bar']},
            restore: {show: true},
            saveAsImage: {show: true}
          }
        },
        legend: {
          show: false,
          data: legend,
          left: 'center',
          top: '10%'
        },
        xAxis: [
          {
            type: 'category',
            data: xAxis,
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              interval: 0,
              rotate: 45
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '件数',
            min: 0,
            // max: 250,
            // interval: 50,
            minInterval: 1,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: yAxis1.name,
            type: 'bar',
            data: yAxis1.data,
            barWidth: '50%',
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: 'top',
                  textStyle: { // 数值样式
                    color: 'red',
                    fontSize: 16
                  }
                }
              }
            }
          }
        ]
      }
      Chart.setOption(option)
    },
    cellClickFn (index, row) {
      console.log(1, row)
      sessionStorage.detailsMatter = row.matter
      store.commit('changeDetailsMatter')
      this.$router.push('/dataAnalysis/matterDetails')
    }
  }
}
</script>

<style lang="scss" scoped>
    #dataAnalysis{
        .itemWarp{
            border: 1px solid #b4b4b4;
            /*border-radius: 4px;*/
            text-align: left;
        }
        .todayTitle{
            padding: 10px 0 10px 10px;
            font-size: 20px;
        }
        .todayContent{
            padding: 0 0 10px 40px;
            font-size: 20px;
            color: red;
            height: 33px;
        }
        .picTitle{
            padding-left: 20px;
            border-left: 4px solid red;
            margin: 60px 0 20px 0;
            text-align: left;
        }
        .picWarp{
            height: 30vw;
            background: rgba(215, 211, 213, 0.53);
        }
        .picRow{
            width: 100%;
            min-height: 30vw;
            border-left: 3.5vw solid transparent;
            border-right: 3.5vw solid transparent;
            background: rgba(215, 211, 213, 0.53);
        }
        .picRowS{
            width: 100%;
            min-height: 30vw;
            padding: 20px 0 20px 0;
            border-left: 3.5vw solid transparent;
            border-right: 3.5vw solid transparent;
            background: rgba(215, 211, 213, 0.53);
        }
    }
</style>



